    computeYear: function computeYear() {
      var returnAge;
      var birth = this.personalInfo.birthday;
      console.log('=======111', birth);
      if (birth && birth.toString().indexOf('-') === -1) {
        //假如日期格式不是2017-8-3这种形式，则进行转日期格式
        this.ChangeBirthday();
        // console.log('非格式日期格式！');
      }
      // Wed Aug 16 1978 00:00:00 GMT+0800 (中国标准时间)没办法进行切割
      // 需要转出2017-8-3
      var strBirthdayArr = birth.toString().split("-");
      var birthYear = strBirthdayArr[0];
      var birthMonth = strBirthdayArr[1];
      var birthDay = strBirthdayArr[2];

      var d = new Date();
      var nowYear = d.getFullYear();
      var nowMonth = d.getMonth() + 1;
      var nowDay = d.getDate();

      if (nowYear == birthYear) {
        returnAge = 0; //同年 则为0岁  
      } else {
        var ageDiff = nowYear - birthYear; //年之差  
        if (ageDiff > 0) {
          if (nowMonth == birthMonth) {
            var dayDiff = nowDay - birthDay; //日之差  
            if (dayDiff < 0) {
              returnAge = ageDiff - 1;
            } else {
              returnAge = ageDiff;
            }
          } else {
            var monthDiff = nowMonth - birthMonth; //月之差  
            if (monthDiff < 0) {
              returnAge = ageDiff - 1;
            } else {
              returnAge = ageDiff;
            }
          }
        } else {
          returnAge = -1; //返回-1 表示出生日期输入错误 晚于今天  
        }
      }

      // this.yearOld=returnAge;//返回周岁年龄 
      return returnAge;
    }
